.homeContainer {
  background-color: rgb(10, 35, 57);
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}
.heroSection {
  background-color: rgb(255, 255, 255);
  height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
  padding-top: 2%;
  padding-bottom: 2%;
}
.heroSectionleft {
  width: 50%;
  height: "100%";
  background-color: "red";
  padding-left: 4em;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  min-width: 300px;
}
.heroSectionRight {
  width: 50%;
  height: 100%;
  background-color: "green";
}
.heroImg {
  width: 100%;
}
.homeContainer .testomonialSection {
  background-color: rgb(132, 159, 100);
  height: 100vh;
  width: "100%";
  display: flex;
}
.homeContainer .courseSection {
  background-color: rgb(0, 21, 40, 1);
  /* height: 100vh; */
  width: "100%";
  display: flex;
  flex-wrap: wrap;
  padding: 2em 0;
}

.courseSection_left {
  width: 50%;
  min-width: 300px;
  height: "100%";
  background-color: "red";
  padding-left: 4em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 300px;
}
.courseViewSection {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: auto;
}
.courseContainer {
  display: flex;

  justify-content: space-between;
  height: auto;
}

.opnclsImg {
  width: 10%;
  height: 20%;
}

.courseSection_right {
  width: 50%;
  height: 100%;
  background-color: "green";
}

.courseSection_right {
  width: 50%;
  height: "100%";
  background-color: "red";
  padding-left: 4em;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  min-width: 300px;
}

.opnclsImg {
  width: 5%;
  height: 5%;
  padding-bottom: 0%;
}

.courseSection_left {
  width: 50%;
  height: 100%;
  background-color: "green";
}

.homeContainer .feedBackSection {
  background-color: rgb(24, 32, 58);
  height: 100vh;
  width: "100%";
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.feedBackSectionleft {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 4em;
}
.feedBackSectionRight {
  width: 50%;
  height: 100%;
  position: relative;
}
.getStatedSection {
  background-color: rgb(22, 66, 107);
  height: 30vh;
  width: "100%";
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  position: relative;
}
.getStatedSection .dotedImg {
  position: absolute;
  width: 40%;
  height: 90%;
  right: 0;
  bottom: 0;
  /* z-index: -2; */
}
.feedBackImg {
  width: 40rem;
  position: absolute;
  top: 10%;
  right: 10%;
  margin-bottom: 10%;
}
.circleImg {
  position: absolute;
}
.parterlogoImg {
  width: 60%;
}
@media screen and (max-width: 850px) {
  .heroSectionleft {
    width: 100%;
    padding: 0;
    margin: 0 1em;
  }
  .heroSectionRight {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .heroImg {
    width: 50%;
  }
